import { createWebHashHistory, createRouter } from "vue-router";

const routes = [
    {
        path: "/",
        redirect: '/home'
    },
    {
        path: "/home",
        name: "home",
        component: () => import('../pages/Home')
    },
    {
        path: "/investments",
        name: "investments",
        component: () => import('../pages/Investments')
    },
    {
        path: "/invoice",
        name: "invoice",
        component: () => import('../pages/Invoice')
    },
    {
        path: "/fines",
        name: "fines",
        component: () => import('../pages/Fines')
    },
    {
        path: "/taxes",
        name: "taxes",
        component: () => import('../pages/Taxes')
    },
    {
        path: "/transactions",
        name: "transactions",
        component: () => import('../pages/Transactions')
    },
];

const router = createRouter({
    history: createWebHashHistory(),
    routes,
    mode: "hash"
});

export default router; 