<template>
    <li @click="requests[props.item]">
        <img :class="{'item-selected': props.selected}" :src="require(`../images/${props.item}.svg`)" />
        <div class="bar" v-if="props.selected"></div>
    </li>
</template>

<script setup>
    import { defineProps } from 'vue';
    import { useStore } from 'vuex';
    import post from '../../../utils/post';
    import { useRouter } from 'vue-router';

    const router = useRouter();
    const store = useStore();

    const requests = {
        home: async () => {
            const res = await post('Home');
            if(res){
                store.state.initial = res;
            }
            router.push(`/home`)
        },

        investments: async () => {
            const res = await post('Investments');
            if(res){
                store.state.initial.Investments = res;
            }
            router.push(`/investments`)
        },

        fines: async () => {
            const res = await post('FineList');
            if(res){
                store.state.initial.Fines = res;
                const total = res.reduce((a,b) => b.value + a, 0);
                store.state.initial.totalFines = total;
            }
            router.push(`/fines`)
        },

        taxes: async () => {
            const res = await post('Taxes');
            if(res){
                store.state.initial.Taxes = res;
            }
            router.push(`/taxes`)
        },

        invoice: async () => {
            const res = await post('InvoiceList');
            if(res){
                store.state.initial.InvoiceList = res;
            }
            router.push(`/invoice`)
        },
        
        transactions: async () => {
            const res = await post('TransactionHistory');
            if(res){
                store.state.initial.TransactionHistory = res;
            }
            router.push(`/transactions`)
        }
    }

    const props = defineProps({
        selected: Boolean,
        item: String
    });
</script>

<style scoped>
    li {
        position: relative;
        display: flex;
        align-items: center;
    }
    
    li img {
        width: 1.51vw;
        opacity: .5;
        cursor: pointer;
        transition: all ease .4s;
    }

    li:hover img {
        opacity: 1;
    }
    
    .bar {
        width: 0.12416666666666667vw;
        height: 0.6770833333333334vw;
        background-color: #FFF;
        position: absolute;
        right: -0.625vw;
    }

    .item-selected {
        opacity: 1;
    }
</style>