<script setup>
    import { onMounted, ref } from 'vue';
    import { useStore } from 'vuex';
    import Header from './components/Header';
    import NavbarMenu from './components/NavbarMenu';
    import MyCard from './components/MyCard/index.vue';
    import post from './utils/post';
    import router from './routes/routes';

    const store = useStore();

    onMounted(() => {
        window.addEventListener('message', ({ data }) => {
            switch (data.Action) {
                case 'Open':
                    store.state.opened = true;
                    store.state.name = data.name;
                    (async () => {
                        const res = await post('Home');
                        if(res){
                            store.state.initial = res;
                        }
                    })()
                    // router.push('/home');
                    break;
                case 'Hide':
                    store.state.opened = false;
                    break;
                    
                default:
                    break;
                }
            });
                
        window.addEventListener('keydown', async (e) => {
            if(e.keyCode === 27) {
                const res = await post('Close');
                if(res){
                    router.push('/home');
                }
            }
        });

        window.addEventListener("online", function(){
            if(!store.state.internetStatus){
                store.state.InternetStatus = true
            }
        })

        window.addEventListener("offline", function(){
            if(store.state.internetStatus){
                store.state.InternetStatus = false
            }
        })
    });
</script>

