<template>
    <button class="button-dependents" @click="handleClick">
        <img v-if="props.img" :src="props.img" />
        {{props.text}}
    </button>
</template>

<script setup>
    import { defineProps } from 'vue';

    function handleClick(){
        if(props.args){
            props.cb(...props.args);
            return;
        }

        props.cb();
    }

    const props = defineProps({
        img: String,
        text: String,
        cb: Function,
        args: Array
    });
</script>

<style scoped>
    .button-dependents {
        width: max-content;
        padding: 0.8854166666666667vw 0.9895833333333334vw;
        border: none;
        outline: none;
        background: rgba(0, 157, 105,.1);
        border-radius: 0.36458333333333337vw;
        font-size: 0.8333333333333334vw;
        font-weight: 600;
        color: rgba(0, 188, 126, 1);
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 0.8333333333333334vw;
        cursor: pointer;
        transition: all ease .4s;
        text-transform: uppercase;
    }

    .button-dependents:hover {
        background: rgba(0, 188, 126, 0.15);
    }

    .button-dependents:active {
        background: rgba(0, 188, 126, 0.2);
    }

    .button-dependents img {
        height: 1.25vw;
    }
</style>