<script setup>
    import { ref } from 'vue';
    import { useStore } from 'vuex';
    import Button from '../Button';
    import User from './images/userCard.svg';
    import formatMoney from '../../utils/formatMoney';
    import post from '../../utils/post';

    const store = useStore();
    const consultOpened = ref(false);
    const dependentPassport = ref('');

    function addDependentRedirect(){
        consultOpened.value = true;
    }

    function consultDependentsHandler(){
        consultOpened.value = !consultOpened.value;
    }

    async function removeDependentHandler(passport) {
        const res = await post('RemoveDependents', { passport });
        if(res){
            const index = store.state.initial.dependents.findIndex(el => el.passport === passport);
            store.state.initial.dependents.splice(index, 1);
        }
    }

    async function addDependentHandler(){
        const passport = parseInt(Math.abs(dependentPassport.value));
        const res = await post('AddDependents', { passport });
        if(res){
            dependentPassport.value = '';
            store.state.initial.dependents.push({ name: res, passport });
        }
    }
</script>

<template>
    <div class="mycard">
        <div v-if="!consultOpened">
            <p>Meu cartão</p>
            <div class="card-frame">
                <p class="card-number">
                    <span>{{store.state.initial.cardnumber?.split(' ')[0]}}</span>
                    <span>{{store.state.initial.cardnumber?.split(' ')[1]}}</span>
                    <span>{{store.state.initial.cardnumber?.split(' ')[2]}}</span>
                    <span>{{store.state.initial.cardnumber?.split(' ')[3]}}</span>
                </p>
                <p class="due-date">12/27</p>
                <p class="ccv">CCV ***</p>
            </div>
            <div class="spending-frame">
                <p class="title-spending">Gastos</p>
                <p>{{formatMoney(store.state.initial?.spending)}}</p>
            </div>
            <div class="limited-frame">
                <p class="title-limited">Limite</p>
                <p>{{formatMoney(store.state.initial?.cardlimit)}}</p>
            </div>
            <p class="my-dependents">Meus dependentes</p>
            <div class="consult-frame" @click="consultDependentsHandler">
                <p>Consultar dependentes</p>
                <img src="./images/arrow.svg" />
            </div>
            <Button :cb="addDependentRedirect" style="margin-top: 2.9vw" :img="User" text="NOVO DEPENDENTE"/>
        </div>
        <div class="consult-frame-box" v-if="consultOpened">
            <div class="consultTitle">
                <div class="goBackBox" @click="consultOpened = !consultOpened">
                    <img src="./images/left-arrow.svg">
                    <p>Voltar</p>
                </div>
                <p class="myDependentsTitle">Meus Dependentes</p>
            </div>
            <div class="dependentsList">
                <div class="dependent" v-for="(dependent, key) in store.state.initial?.dependents" :key="key">
                    <p>{{dependent.name}}</p>
                    <Button :cb="removeDependentHandler" :args="[dependent.passport]" class="removeDependent" text="REMOVER"/>
                </div>
            </div>
            <p>Adicionar Dependente</p>
            <div class="addDependentBox">
                <p>Qual é o passaporte do dependente?</p>
                <input type="text" v-model="dependentPassport" placeholder="3234"/>
                <Button :cb="addDependentHandler" class="addButton" text="ADICIONAR"></Button>
            </div>
        </div>
    </div>
</template>

